.form {
  width: 500px;
  flex-direction: column;
  gap: 10px;
  margin: 0 auto;
  display: flex;
}

.form input {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 5px;
  padding: 10px 15px;
}

.form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px 15px;
  transition-duration: .25s;
}

.form button:hover, .form button:focus {
  background-color: #0069d9;
}

